import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { ConfigService } from './config.service';
import { map } from 'rxjs/operators';
import { Router } from '@angular/router';
import { User } from '../model/user';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  // rtesteni kell az alkalmazs hasznlit, hogy be van-e lpve vagy nem
  currentUserSubject$: BehaviorSubject<User | null> =     // ha be van lpve akkor User, ha nincs akkor null-t kapunk vissza
    new BehaviorSubject<User | null>(null);     // kezdsre null, mert alapbl nincs senki belpve
  lastToken: string = '';       // az utols trolt token
  loginUrl: string = `${this.config.apiUrl}login`;                  // erre kldi a postot a bejelentkezsi cmre


  constructor(
    private config: ConfigService,
    private http: HttpClient,
    private router: Router,
  ) {
    if (localStorage.currentUser) {
      const user: User = JSON.parse(localStorage.currentUser);    // van-e belpett felhasznlm a localStorage-ban, gy vissza tudom tlteni
      this.lastToken = user.accessToken || '';
      this.currentUserSubject$.next(user);
    }
  }

  // npm install bcryptjs

  login(loginData: User): Observable<User | null> {
    return this.http.post<{ user: User, accessToken: string }>(       // postolunk mert be akarunk jelentkezni, az accesstokent s a usert kld vissza a szerver
      this.loginUrl,   // hova
      loginData       // mit
    ).pipe(           // ez a pipe, azrt kell, mert a login tpusa, nem egyezik a post tpusval
      map(response => {     // mindez ha sikerlt belpni
        if (response.user && response.accessToken) {
          this.lastToken = response.accessToken;
          response.user.accessToken = response.accessToken;
          this.currentUserSubject$.next(response.user);   // a feliratkozknak tertem, teht akik figyelik az auth service-emet, kapnak rtestst rla, hogy vltozott currentuserSubject rtke
          localStorage.currentUser = JSON.stringify(response.user);        // a bngsz localeStorage-ba tarolom, a user adatait. Ha frisstem a bngszt, akkor meg tudom elbb nzni a localeStorage-t s nem kell jra lekrdezni, viszont, ezt majd egyszer trlni is kell.
          return response.user;   // Usert ad vissza
        }
        return null;      // ha nem akkor null-t
      })           // feliratkozik ennek a postnak a kimenetre, egy ilyet fog kapni, (user s access token) s visszaad valami ms adatot, vagyis pont a <User | null> tpust
    );
  }

  logout(): void {
    this.lastToken = '';                              // mindent nullzunk
    this.currentUserSubject$.next(null);
    localStorage.removeItem('currentUser');
    this.router.navigate(['/', 'login']);             // visszairnyt a login oldalra
  }

}
